
import Badge from 'vux/src/components/badge/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'


const { _ } = window

export default {
props: {
list: {
type: Array,
default: () => []
}
},
components: {
Grid,
GridItem,
Badge
},
computed: {
computedList () {
const size = 3
const placeholder = _.map(Array.apply(null, { length: size }), () => null)
return _(this.list).concat(placeholder).chunk(size).slice(0, -1).value()
}
}
}



